export default {
	data() {
		return {
			lists:[]
		}
	},
	methods: {
		getStatusStr(status){
			var str='';
			// 1充值 2投注 3退分 4赢 5输 6平局
			switch (status){
				case '1':
					str = '充值';
					break;
				case '2':
					str = '投注';
					break;
				case '3':
					str = '退分';
					break;
				case '4':
					str = '赢';
					break;
				case '5':
					str = '输';
				case '6':
					str = '平局';
					break;
				default:
					break;
			}
			return str;
		},
		getList(current,size,callback)
		{
			this.bus.request({
				url: this.bus.url+'/agent/membersPointRecord',
				method: 'GET',
				data: {
					userId:this.bus.user.userId,
					current:current,
					size:size
					},
				success: res => {
					res = res.data;
					if(res.status=='1')
					{
						// uni.showToast({
						// 	title: '密码修改成功!',
						// 	duration: 2000
						// });
						callback(res.data.records)
					} else{
						uni.showToast({
							title: res.errMsg,
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					
				},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	},
	onLoad:function () {
		uni.startPullDownRefresh();
	},
	onReachBottom:function()
	{//上拉加载更多
		var current=this.lists.length;
		var	size=10;
		var this_ = this;
		this.getList(current,size,function (data) {
			for (var i = 0; i < data.length; i++) {
				this_.lists.push(data[i]);
			}
		})
	},
	onPullDownRefresh:function () {
		//下拉刷新
		var current=0;
		var	size= 10;
		if(this.lists.length)
		{
			size=this.lists.length;
		}
		var this_ = this;
		this.getList(current,size,function (data) {
			this_.lists=data;
		})
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	}
}
